<template>
	<div class="acc_wrap">
		<div class="public_head head">
            <Headtop></Headtop>
        </div>
        <div class="content">
            <div class="the_wrap">
                <div class="back">
                    <div class="left_box">
                        <div class="sum">
                            <span></span><span>10014.52</span>
                        </div>
                        <p class="can"></p>
                    </div>
                    <div class="right_box">
                        <div class="sum">
                            <span></span><span>10014.52</span>
                        </div>
                        <p class="can"></p>
                    </div>
                </div>
                <!--  -->
                <div class="btn_wrap">
                    <!--  -->
                    <router-link to="/index/user/topUpAmount">
                        <div class="top-up"></div>
                    </router-link>
                    <!--  -->
                    <router-link to="/user/withdrawal">
                        <div class="draw"></div>
                    </router-link>    
                </div>
                <p class="pour"></p>
                <!--  -->
                <div class="detail_wrap">
                    <!--  -->
                    <router-link to="/user/theWithdrawal">
                        <div class="dep">
                            <div class="the"></div>
                            <div class="icon"></div>
                        </div>
                    </router-link>
                    <!--  -->
                    <router-link to="/user/topUpDetail">
                        <div class="dep">
                            <div class="the"></div>
                            <div class="icon"></div>
                        </div>
                    </router-link>    
                    <!--  -->
                    <router-link to="/user/billingDetails">
                        <div class="dep">
                            <div class="the"></div>
                            <div class="icon"></div>
                        </div>
                    </router-link>    
                </div>
                <!--  -->
                <div class="map_wrap">
                    <img src="/static/img/public/getinto_share_icon.png"/>
                </div>
            </div>
        </div>
	</div>
</template>

<script>
	/** */
	import Headtop from '@/components/public/page_top_head'
	export default {
        name: 'theAccountBalance',
        components: {
			Headtop,
		},
		data() {
			return{
                
			}
		},
	
		/*DOM*/
		created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: true,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

		},
		methods: {
		}
	}
</script>

<style lang="stylus" scoped>
    .acc_wrap
        width 100%
        height 100%
        background #fff
        .head
            border-bottom 1.1px solid #e6e6e6
        .content
            .the_wrap
                margin 22px 24px
                .back
                    padding 90px 0
                    width 100%
                    background url('/static/img/user/my/bjtu.png') no-repeat
                    background-size 100% 100%
                    font-size 0
                .back .left_box,.right_box
                    display inline-block
                    vertical-align top
                    width 50%
                    text-align center
                .sum
                    font-size 40px
                    color #fff
                    line-height 85px
                .can
                    font-size 26px
                    color #ffe1e2
                    line-height 45px
                .btn_wrap
                    margin 25px 0 30px
                .btn_wrap .top-up,.draw
                    display inline-block
                    vertical-align top
                    width 340px
                    height 80px
                    border-radius 40px
                    font-size 26px
                    text-align center
                    line-height 80px
                    letter-spacing 4px
                .top-up
                    background #ff4902
                    color #fff
                .draw
                    margin 0 0 0 10px
                    border 1.1px solid #ff4902
                    color #ff4902
                .pour
                    color #ff5559
                    font-size 24px
                    line-height 55px
            .detail_wrap
                margin 55px 0
                .dep div
                    display inline-block
                    vertical-align middle
                .dep
                    margin 0 0 15px 0
                    padding 0 33px
                    font-size 26px
                    color #151515
                    line-height 80px
                    -webkit-box-shadow: 0px 0px 5px 0px #cccccc;
                    box-shadow: 0px 0px 5px 0px #cccccc;
                    border-radius: 6px;    
                .icon
                    float right
                    margin 36px 0 0 
                    width 8px
                    height 14px
                    background url('/static/img/user/my/left.png') no-repeat
                    background-size 100% 100%
            .map_wrap
                margin 10px 0 50px
                img
                    display block
                    margin 0 auto 
                    max-width 100%
                    border-radius 10px 

</style>


