<template>
    <div class="max_div">
            <!--  -->
            <headtop></headtop>
            <ul class="ul">
                     <li class="li">
                         <span class="user_id"></span><span class="user_name"></span><span class="user_phone"></span>
                     </li>
                     <li class="li">
                          <span class="user_id">44088214489578</span><span class="user_name">89</span><span class="user_phone">18488885975</span>
                     </li>
                     <li class="li">
                          <span class="user_id">789895</span><span class="user_name">89</span><span class="user_phone">18488885975</span>
                     </li>
            </ul>
  
 

           

    </div>
</template>

<script>
/** */
	import headtop from '@/components/public/page_top_head'
    export default {
		name: 'commissionlist',
		data(){
            return{
                 active: 0
            }
			
		},
        components:{
            headtop
        },
        created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: false,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

        },
        methods:{
             


        }
    }
</script>

<style lang="stylus" scoped>
.max_div
    position absolute
    left 0
    right 0
    bottom 0
    top 0
    background white
    .ul
        width 90%
        margin 100px 0 0 5%
        .li
            width 100%
            padding 10px 0 6px 0
            .user_id
                display inline-block
                vertical-align top
                width 35%
                text-align center
                font-size 20px
            .user_name
                display inline-block
                vertical-align top
                width 35%
                text-align center 
                font-size 20px       
            .user_phone
                display inline-block
                vertical-align top
                width 30%
                text-align center 
                font-size 20px   
        .li:nth-child(odd)
            background #f3f9ff
        .li:nth-child(even)
            background #ecf4fc   
        .li:first-child  
            background #c6e1ff 

        

</style>



