<template>
    <div class="flash-sale">
        <!--  -->
        <headtop></headtop>
        <!--  -->
        <div class="content">
            <!--  -->
            <div class="advert">
                <img src="/static/img/flashSale/00advert-img01.png" />
            </div>

            <!--  -->
            <div class="flash-sale-list">
                <div class="goods-item">
                    <router-link to="/home/flashSale/flashDetails">
                        <div class="img-wrap">
                            <img src="/static/img/flashSale/00flash-sale-img01.png" />
                        </div>
                    </router-link>
                    <div class="text">
                        <router-link to="/home/flashSale/flashDetails">
                            <h3></h3>
                        </router-link>
                        <div class="price">
                            <span class="discount-price"><i class="yen"></i>360.00</span>
                            <span class="original-price">700.00</span>
                        </div>
                        <div class="progress-wrap">
                            <van-progress
                                pivot-text=""
                                color="#ff0b13"
                                :percentage="50"
                            />
                            <div class="robbed-num">616</div>
                        </div>
                        <div class="endTime">
                            : <span>23:23:55</span>
                        </div>
                        <div class="buyBtn">
                             &gt;
                        </div>
                    </div>
                </div>
                <div class="goods-item">
                    <div class="img-wrap">
                        <img src="/static/img/flashSale/00flash-sale-img01.png" />
                    </div>
                    <div class="text">
                        <h3></h3>
                        <div class="price">
                            <span class="discount-price"><i class="yen"></i>360.00</span>
                            <span class="original-price">700.00</span>
                        </div>
                        <div class="progress-wrap">
                            <van-progress
                                pivot-text=""
                                color="#ff0b13"
                                :percentage="50"
                            />
                            <div class="robbed-num">616</div>
                        </div>
                        <div class="endTime">
                            
                        </div>
                        <div class="buyBtn">
                             &gt;
                        </div>
                    </div>
                </div>
                <div class="goods-item">
                    <div class="img-wrap">
                        <img src="/static/img/flashSale/00flash-sale-img01.png" />
                    </div>
                    <div class="text">
                        <h3></h3>
                        <div class="price">
                            <span class="discount-price"><i class="yen"></i>360.00</span>
                            <span class="original-price">700.00</span>
                        </div>
                        <div class="progress-wrap">
                            <van-progress
                                pivot-text=""
                                color="#ff0b13"
                                :percentage="50"
                            />
                            <div class="robbed-num">616</div>
                        </div>
                        <div class="endTime">
                            : <span>23:23:55</span>
                        </div>
                        <div class="buyBtn">
                             &gt;
                        </div>
                    </div>
                </div>

                <!--  -->
                <div class="end-wrap">
                    <p>~~</p>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
import headtop from '@/components/public/page_top_head'
export default {
    name:'flashSale',
    components: {
        headtop
    },
    data(){
        return{
            //
			page:1,
        }
    },
    /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#f8f8f8',
            },
            return_data: {
                need_model: true,
                icon: '/static/img/public/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**-text */
			    change_text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);

    },
    mounted(){
        this.requestData()
    },
    methods:{
        // 
        requestData(){
            let url = "/Goods/limited_list"
            let params = new URLSearchParams()
                params.append('page', this.page)
            this.$axios({
                url:url,
                method:'get',
                data:params
            })
            .then( (res) =>{
                console.log(res)
            })
            .catch((error) =>{
                console.log(error)
            })
        }
    }

}
</script>

<style lang="stylus" scoped>
.content
    padding 88px 24px 0
    box-sizing border-box
    .advert
        img
            width 100%
            border-radius 8px
    .flash-sale-list
        .goods-item
            display flex
            padding 20px
            box-sizing border-box
            position relative
            box-shadow 0 0 8px #e6e6e6
            background-color #fff
            margin-bottom 14px
            .img-wrap
                width 240px
                height 240px
                margin-right 28px
                img 
                    max-width 100%
                    max-height 100%
                    display inline-block
            .text
                flex 1
                h3
                    font-size 24px
                    color #151515
                    line-height 30px
                    font-weight normal
                    display -webkit-box
                    -webkit-box-orient vertical
                    -webkit-line-clamp 2
                    overflow hidden
                .price
                    line-height 44px
                    .discount-price
                        color #ff112f
                        font-size 36px
                        .yen
                            font-size 20px
                            font-style normal
                    .original-price
                        color #929292
                        font-size 18px
                        text-decoration line-through
                .progress-wrap
                    height 46px
                    display flex
                    align-items center
                    .van-progress
                        width 195px
                        height 10px
                        background-color #ff6742
                    .robbed-num
                        margin-left 20px
                        font-size 24px
                        color #151515
                .endTime
                    font-size 24px
                    color #151515
                .buyBtn
                    width 150px
                    height 50px
                    line-height 50px
                    background-color #ff0000
                    color #ffffff
                    font-size 30px
                    text-align center
                    border-radius 5px
                    position absolute
                    right 20px
                    bottom 20px
        .end-wrap
            font-size 18px
            color #888888
            text-align center
            margin 30px auto

</style>



