<template>
    <div class="top_wrap">
        <div class="public_head head">
            <HeadTop></HeadTop>
        </div>
        <div class="content">
            <div class="col_wrap">

                <div class="inp">
                    <input type="text" placeholder=""/>
                </div>

                <div class="list_wrap">
                    <ul class="list">
                        <li v-for="(item,index) in payment" :key="index" @click="cur=index" :class="{active:cur==index}">
                            <div class="red">{{item.jin}}</div>
                            <div class="into">{{item.e}}</div>

                            <div class="hook_wrap" v-show="cur==index"></div>
                        </li>
                    </ul>
                </div>
                <!--  -->
                <div class="up_btn"></div>
                <!--  -->
                <div class="map_wrap">
                    <img src="/static/img/public/getinto_share_icon.png"/>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    /** */
	import HeadTop from '@/components/public/page_top_head'
	export default {
        name: 'topUpAmount',
		data() {
			return{
                payment:[
                    {id:1,jin:'50',e:'100'},
                    {id:2,jin:'100',e:'150'},
                    {id:3,jin:'200',e:'350'},
                    {id:4,jin:'500',e:'950'},
                    {id:5,jin:'1000',e:'1850'},
                    {id:6,jin:'2000',e:'3500'},
                ],
                //
                cur: 0 
			}
		},
		components: {
			HeadTop,
		},
		/*DOM*/
		created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: true,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

		},
		methods: {
            // //
            // selectPay(item,index){
            //     this.indexs = index  
            //     if(index === this.indexs ){
            //         if (item.cheack) {                            
            //             item.cheack = false;  
            //         } else {
            //             for (let i = 0; i < this.payment.length; i++) {
            //                 this.payment[i].cheack = false;
            //             }
            //                 item.cheack = true;   
            //         }
            //     }
            // },
		}
	}
</script>

<style lang="stylus" scoped>
    .top_wrap
        width 100%
        height 100%
        background #fff
        .head
            border-bottom 1.1px solid #e6e6e6
        .content
            .col_wrap
                margin 24px 24px
                .inp
                    margin 0 0 30px
                    padding 26px 0
                    width 100%
                    border-bottom 1.1px solid #e6e6e6
                .inp input
                    padding 0 0 0 15px
                    width 85%
                    border none
                    outline none
                    line-height 55px
                    font-size 26px
                    color #151515
                .inp_wrap input::-webkit-input-placeholder
                    font-size 26px
                    color #717171
                .list_wrap
                    width 100%
                    font-size 0
                .list_wrap .list li
                    position relative
                    margin 0 17px 20px 0
                    display inline-block
                    vertical-align top
                    width 220px
                    height 100px
                    border-radius 6px
                    text-align center
                    border 1.1px solid #ff4902
                    .red
                        margin 15px 0 0
                        font-size 30px
                        font-weight 550
                        color #ff112f
                        line-height 35px
                    .into
                        font-size 26px
                        color #151515
                        line-height 35px
                .hook_wrap
                    position absolute
                    bottom 0
                    right 0
                    width 41px
                    height 31px
                    background url('/static/img/user/my/duigou.png') no-repeat
                    background-size 100% 100%

                .list_wrap .list li:nth-child(3)
                    margin 0 0 20px 0
                .list_wrap .list li:nth-child(6)
                    margin 0 0 20px 0
                .up_btn
                    margin 37px auto 43px
                    width 702px
                    height 80px
                    background #ff4902
                    border-radius 40px
                    color #fff
                    font-size 26px
                    text-align center
                    line-height 80px
                    letter-spacing 4px   
                .map_wrap
                    margin 10px 0 50px
                    img
                        display block
                        margin 0 auto 
                        max-width 100%
                        border-radius 10px    


</style>


