<script>
import Vue from 'vue'
import AreaList from './area'
import {GoodsAction,GoodsActionBigBtn,GoodsActionMiniBtn,Toast,Sku } from 'vant'

Vue.use(
    GoodsAction,
    GoodsActionBigBtn,
    GoodsActionMiniBtn,
    Toast,
    Sku 
    )

export default {
    name:'flashDetails',
    components:{
        GoodsAction,
        GoodsActionBigBtn,
        GoodsActionMiniBtn,
   
    },
    data(){
        return {
            tabActive: 0,//tab
            rateVal: 3,//
            areaShow:false,//
            address:'',//
            areaList:AreaList,// 
            couponShow:false,//
            skuShow:false,//
            value: 1,//

            
            showBase:false,
            sku: {
                // sku
                // 
                tree: [
                    {
                        k: '', // skuKeyName
                        v: [
                            {
                            id: '30349', // skuValueId id
                            name: '', // skuValueName
                            imgUrl: 'https://img.yzcdn.cn/1.jpg' // 
                            },
                            {
                            id: '1215',
                            name: '',
                            imgUrl: 'https://img.yzcdn.cn/2.jpg'
                            }
                        ],
                        k_s: 's1' // skuKeyStrsku  list key value  id
                    },
                    {
                        k: '',
                        v: [
                            {
                                id: '1193',
                                name: 'S',
                            },
                            {
                                id: '222',
                                name: 'M',
                            }
                        ],
                        k_s: 's2'
                    }
                ],
                //  sku M  sku S 
                list: [
                    {
                    id: 2259, // skuId
                    price: 100, // 
                    s1: '1215', //  k_s  s1  id
                    s2: '1193', //  k_s  s2  id
                    s3: '0', // 30
                    stock_num: 110 //  sku 
                    }
                ],
                price: '1.00', // 
                stock_num: 227, // 
                collection_id: 2261, //  skuId  collection_id sku  id
                none_sku: false, // 
                hide_stock: false, // 
                goods: {
                    // 
                    title: '',
                    //  sku 
                    picture: 'https://img.yzcdn.cn/2.jpg'
                },
            },

        }
    },
    methods:{
        // 
        areaSelect(){
            this.areaShow = true
        },
        //
        onAddrConfirm(val){  
            this.areaShow = false;
            this.address = val[0].name+ val[1].name +val[2].name
        },
        //
        onAddrCancel(){  
            this.areaShow = false
        },
        // 
        handleBtn(){
            // this.skuShow = true
            this.showBase = true
        },
        // 
        close(){
            this.skuShow = false
        },
        // 
        confirm(){
            this.close()
        }


    }
}
</script>

