/*(vue)*/
import Vue from 'vue';
/* vue */
import Vuex from 'vuex'
/* */
import router from 'vue-router';

/*vue  vuex */
Vue.use(Vuex);
/**
 * ()
 * ::()
 * **/
function clones(_type, _memory) {
	var newMemory = null;
	/**/
	if(_type instanceof Array) {
		newMemory = _memory || [];
	}else {
		newMemory = _memory || {}
	}
	for(var x in _type) {
		/*typeofobject*/
		if(typeof _type[x] == "object") {
			newMemory[x] = (_type[x].constructor == Array) ? [] : {};
			clones(_type[x], newMemory[x]); //
		}else {
			/**/
			newMemory[x] = _type[x];
		}
	}
	return newMemory;
}



/*=>*/
const state = {
	/*input=>(:true)*/
	inpShowEle: true,
	/**--*/
	top_head_data: {
		/**headstyle */
		head_wrap_style: {
			background: '',
		},
		return_data: {
			/**- */
			need_model: true,
			icon: '/static/img/public/left_icon_555.png',
			/*()=>*/
			url_data: '',
		},
		title: {
			need_model: true,
			name: '',
			style: {color:'#333'}
		},
		right_button: {
			need_model: false,
			/**=>=>  */
			text: '',
			/**-text */
			change_text: '',
			/**=>(styleclass) */
			icon: {
				class: '',
				icon_url: '',
			},
			/**-(=>[])*/
			show: false,
			url_data: '',
		},
	},
	/** */
	page_loadding: {
		/** */
		tips_text: '',
		/**false */
		show: true,
		/**=> start,=> end,=>error*/
		state: 'start',
		/**loading_wrap */
		load_wrap_style: {},
		/***=> start  loading icon */
		load_icon_color: '#4a4949',
	},

	

}

/*state */
const mutations = {
	/* public -s */
	/*input => */
	obtainInputX(state) {
		/*state*/
		state.inpShowEle = true;
	},
	/*input => */
	loseInputX(state) {
		state.inpShowEle = false;
	},
	/** */
	change_head(state,style_obj) {
		state.top_head_data = clones(style_obj);
	},
	/**--=> show || hide */
	head_right_but(state) {
		console.log('--=> show || hide');
		if (state.top_head_data['right_button']['show']) {
			state.top_head_data['right_button']['show'] = false;
		} else {
			state.top_head_data['right_button']['show'] = true;
		}	
	
	},
	/** loading  */
	page_load(state,_styleObj){
		state.top_head_data = clones(_styleObj);
	},
	/* public -e*/



	
}


/*  vuex (||)*/
export default new Vuex.Store({
	/**/
	state,
	mutations,
})



