<script>
import headtop from '@/components/public/page_top_head'
export default {
    name:'order',
    components: {
        headtop
    },
    data(){
        return{
            nowIndex:0,
            type:this.$route.query.type,
            // tab
            tabList:[
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                }
            ],
            baseUrl:[],// 
            ispage:true,//
            token:window.sessionStorage.getItem("token")
        }
    },
    /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#ffffff',
            },
            return_data: {
                need_model: true,
                icon: '/static/img/public/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**-text */
			    change_text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);

    },
    methods:{
        // tab
        handleClick(index){
            this.nowIndex = index
            this.$router.push('/order?type='+index)
            this.type = this.$route.query.type
            this.dataRendering()
        },
        // 
        dataRendering(){
            this.baseUrl = this.url
            this.nowIndex = parseInt(this.type)
            var type;
            if (this.nowIndex === 0) {
                type = "all"
            }
            if (this.nowIndex === 1) {
                type = "dfk"
            }
            if (this.nowIndex === 2) {
                type = "dfh"
            }
            if (this.nowIndex === 3) {
                type = "dsh"
            }
            if (this.nowIndex === 4) {
                type = "dpj"
            }
            if(this.ispage){
                var url = 'Order/order_list'
                // var token = window.sessionStorage.getItem("token")
                // console.log(token)
                var params = new URLSearchParams()
                params.append('type', type)
                params.append('page', this.page)
                params.append('token', this.token)
                this.$axios({
                    method: "get",
					url: url,
					data: params
                }).then( (res)=>{
                    console.log(res)
                }).catch((error) => {
                    alert(':'+ error)
                })
            }
        }
    }

}
</script>

<style lang="stylus" scoped>
.order
    .tab-wrap
