<template>
	<div class="wit_wrap">
		<div class="public_head head">
            <HeadTop></HeadTop>
        </div>
        <div class="content">
			<div class="hd_wrap">
				<div class="back">
                    <div class="k_box">
                        <div class="sum">
                            <span></span><span>10014.52</span>
                        </div>
                        <p class="can"></p>
                    </div>
                </div>
				<!--  -->
				<div class="way_wrap">
					<h4></h4>
					<!--  -->
					<div class="mode">
						<div class="wechat" v-for="(item,index) in pay" :key="index" @click="cur=index" :class="{on:cur==index}">
							<img :src="item.img"/>
						</div>
					</div>
					<div class="play_wrap">
						<!-- /- -->
						<div class="sum_wrap" v-for="(list,index) in as" :key="index" v-show="cur===index">
							<h4></h4>
							<!--  -->
							<router-link to="/index/user/alipay" v-if="cur===1">
								<div class="fee_wrap">
									<div class="fee"></div>
									<div class="unit icon"></div>
								</div>
							</router-link>

							<div class="put">
								<div class="inp">
									<input type="text" placeholder=""/>
								</div>
								<div class="all_btn"></div>
							</div>
							<!--  -->
							<div class="fee_wrap">
								<div class="fee">
									<span> </span>
									<span>{{list.num}}</span>
								</div>
								<div class="unit"></div>
							</div>
							<!--  -->
							<div class="fee_wrap">
								<div class="fee">
									<span> </span>
									<span>{{list.mas}}</span>
								</div>
								<div class="unit"></div>
							</div>
						</div>

					</div>
				</div>
				<!--  -->
				<div class="apply_btn"></div>
				<!--  -->
				<div class="map_wrap">
					<img src="/static/img/public/getinto_share_icon.png"/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	/** */
	import HeadTop from '@/components/public/page_top_head'
	export default {
		name: 'withdrawal',
		components: {
			HeadTop,
		},
		data() {
			return{
				pay:[
					{id:1,img:'/static/img/group_area/order/weixi.png'},
					{id:2,img:'/static/img/group_area/order/zfb.png'},
				],
				as:[
					{id:1,num:0,mas:92.20},
					{id:2,num:0,mas:92.20},
				],
                //
                cur: 0
			}
		},
		
		/*DOM*/
		created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: true,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

		},
		methods: {
            
		}
	}
</script>

<style lang="stylus" scoped>
	.wit_wrap
		width 100%
		height 100%
		background #fff
		.head
			border-bottom 1.1px solid #e6e6e6
		.content
			.hd_wrap
				margin 21px 24px 30px
			.back
				padding 90px 0
				width 100%
				background url('/static/img/user/my/bjtu.png') no-repeat
				background-size 100% 100%
			.k_box
				text-align center
				line-height 55px
				.sum
					font-size 40px
					color #fff
				.can
					font-size 26px
					color #ffe1e2
			.way_wrap
				h4
					margin 26px 0 26px
					color #151515
					font-size 26px
					line-height 65px
				.mode
					font-size 0
				.mode img
					margin 0 auto
					display block
					max-width 100%
				.mode .wechat
					margin 0 37px 0 0	
				.mode .wechat,.alipay
					display inline-block
					vertical-align middle
					width 58px
					height 58px
				.mode .on
					width 70px
					height 70px
				.sum_wrap
					.sum_wrap
					.put
						padding 22px 0
						border-bottom 1.1px solid #e6e6e6
					.put .inp,.all_btn
						display inline-block
						vertical-align top
						font-size 24px
						color #151515
					.inp input
						width 560px
						height 45px
						border none
						outline none
						font-size 24px
						color #151515
						.inp input::-webkit-input-placeholder
							font-size 24px
							color #717171
					.all_btn
						float right
						line-height 45px
					.fee_wrap
						padding 22px 0
						line-height 45px
						border-bottom 1.1px solid #e6e6e6
					.fee_wrap .fee,.unit
						display inline-block
						vertical-align top
						font-size 24px
						color #151515
					.unit
						float right
					.icon
						margin 16px 0 0 
						width 8px
						height 14px
						background url('/static/img/user/my/left.png') no-repeat
						background-size 100% 100%
			.apply_btn
				margin 50px auto 45px
				width 702px
				height 80px
				background #ff4902
				border-radius 40px
				font-size 26px
				color #fff
				text-align center
				line-height 80px
				letter-spacing 4px
			.map_wrap
				margin 10px 0 50px
				img
					display block
					margin 0 auto 
					max-width 100%
					border-radius 10px			

</style>


