<template>
    <div class="evaluate">
        <!--  -->
        <headtop></headtop>

        <div class="content">
            <div class="evaluate-wrap">
                <!--  -->
                <div class="textBox">
                    <textarea placeholder=""></textarea>
                </div>
                <!--  -->
                <div class="addPic">
                    <span class="camera-icon"></span>
                    <span></span>
                    <input type="file" class="input-file" multiple="multiple" @change="onRead($event)" accept="image/gif,image/jpeg,image/jpg,image/png" >
                </div>
            </div>

            <div class="rate-wrap">
                <h2></h2>
                <div class="rate-item">
                    <div class="label"></div>
                    <van-rate
                        v-model="rateVal"
                        icon="like-o"
                        void-icon="like-o"
                        color="#f70a0a"
                    />
                </div>
                <div class="rate-item">
                    <div class="label"></div>
                    <van-rate
                        v-model="rateVal2"
                        icon="like-o"
                        void-icon="like-o"
                        color="#f70a0a"
                    />
                </div>
                <div class="rate-item">
                    <div class="label"></div>
                    <van-rate
                        v-model="rateVal3"
                        icon="like-o"
                        void-icon="like-o"
                        color="#f70a0a"
                    />
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import headtop from '@/components/public/page_top_head'
export default {
    name:'evaluate',
    components: {
        headtop
    },
    data(){
        return {
            rateVal:5,   
            rateVal2:0,  
            rateVal3:2   
        }
    },
     /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#ffffff',
                borderBottom :'1px solid #e6e6e6'
            },
            return_data: {
                icon: '/static/img/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);
    },
    methods:{
        onRead(e){
            console.log(e)
    
        }
    }

}
</script>

<style lang="stylus" scoped>
.content
    padding 108px 24px 0
    box-sizing border-box
    .evaluate-wrap
        width 100%
        height 450px
        border 1px solid #a7a7a7
        border-radius 8px
        padding 30px
        box-sizing border-box
        .textBox
            width 100%
            margin-bottom 20px
            textarea
                width 100%
                height 240px
                font-size 26px
                color #888888
                border none 
                background none
                resize none
        .addPic
            width 120px
            height 120px
            display flex
            flex-direction column
            align-items center
            justify-content center
            font-size 26px
            color #888888
            border 1px dashed #595959
            border-radius 5px
            position relative
            .camera-icon
                width 46px
                height 38px
                display block
                background url("/static/img/order/camera-icon.png") no-repeat
                background-size cover
                margin-bottom 20px
            .input-file
                width 100%
                height 100%
                position absolute
                left 0
                top 0
                opacity 0
    .rate-wrap
        h2
            font-size 30px
            margin 30px auto 70px
        .rate-item
            font-size 30px
            height 36px
            line-height 30px
            display flex
            margin-bottom 40px
            .label
                margin-right 44px

            

</style>



