<template>
    <div class="group_info">
        <header>  
            <Pheader></Pheader>
        </header>
        <!--  -->
        <van-swipe class="group_swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item @click="preview(index)" v-for="(image, index) in data.images" :key="index">
                <img v-lazy="image" />
            </van-swipe-item>
        </van-swipe>
        <!--  -->
        <van-image-preview 
            v-model="show"
            :images="data.images"
            :startPosition="index"
            @change="onChange"
            @click="preview(index)"
            >
            <template v-slot:index>{{index+1}}</template>
        </van-image-preview>
        <div class="group_notice">200 90</div>
        <!--  -->
        <van-row>
            <van-col class="group_item" span="19">
                <p class="price"><span class="price_text">{{data.group_item[0].minPrice}} - {{data.group_item[0].maxPrice}}</span> </p>
                <p class="pName">{{data.group_item[0].name}}</p>
            </van-col>
            <van-col class="mark_item" span="5">
                <img class="mark_img" :src="data.group_item[0].codeimg" alt="">
                <span class="mark_text"></span>
            </van-col>
        </van-row>
        <!--  -->
        <van-row class="group_booking_all" type="flex" justify="space-between">
            <div>
                {{data.group_booking_title.sum}}
            </div>
            <div>
                <van-icon name="arrow" />
            </div>
        </van-row>        
        <!--  -->
        <van-row class="group_booking" type="flex" justify="space-between">
            <van-col class="group_booking_left" span="12">
                <img class="group_booking_img" :src="data.group_booking[0].userimg" alt="">
                <span>{{data.group_booking[0].username}}</span>
            </van-col>
            <van-col class="group_booking_right" span="12">
                <p>{{data.group_booking[0].short}}</p>
                <p>{{data.group_booking[0].time_day}}{{data.group_booking[0].time}}</p>
            </van-col>
        </van-row>
        <!--  -->
        <van-row class="group_booking" type="flex" justify="space-between">
            <van-col class="group_booking_left" span="12">
                <img class="group_booking_img" src="static/img/000.png" alt="">
                <span></span>
            </van-col>
            <van-col class="group_booking_right" span="12">
                <p>99</p>
                <p>612:59:11</p>
            </van-col>
        </van-row>
        <!-- tab -->
        <van-tabs style="padding-bottom:46px;" v-model="active">
            <van-tab title="">
                <img src="static/img/000.png" alt="">
            </van-tab>
            <van-tab title="(894)"> 2{{showBase}}</van-tab>
        </van-tabs>
        <!--  -->
        <van-goods-action>
            <goods-action-mini-btn
                icon="like-o"
                text=""
            />
            <goods-action-mini-btn
                icon="service"
                text=""
            />
            <goods-action-big-btn
                type="warning"
                text=""
                @click="all()"
            />
            <goods-action-big-btn
                type="danger"
                text=""
                @click="all()"
            />
        </van-goods-action>
        <!--  -->
        <van-sku
            v-model="showBase"
            :sku="data.sku"
            :goods="data.goods"
        />
    </div>
</template>

<script>
    import Pheader from '@/components/public/page_top_head';
    //
    import Vue from 'vue';
    import {Lazyload,GoodsAction,GoodsActionBigBtn,GoodsActionMiniBtn,Sku,Toast} from 'vant';
    Vue.use(Lazyload,
            GoodsAction,
            GoodsActionBigBtn,
            GoodsActionMiniBtn,
            Sku,
            Toast,);
    export default {
        name:'groupInfo',
        data() {
            return {
                show:  false,
                index: 1,
                showBase: false,
                data:{
                    images:[
                        'static/img/home/00banner-img01.png',
                        'static/img/000.png',
                        'static/img/user/0001.jpg',
                        'static/img/group_area/shop.png',
                    ],
                    // 
                    group_item:[
                        {name:'',minPrice:'200.00',maxPrice:'360.00',codeimg:'static/img/group_area/mark.png',pid:'0001'}
                        ],
                    // 
                    group_booking_title:{
                        // 
                        sum:'1418'
                    },
                    group_booking:[
                        // short    time time_day 
                        {userimg:'static/img/000.png',username:'',short:'49',time:'12:51:45',time_day:'6',bid:'0004'}
                    ],
                    goods: {
                            // 
                            title: '',
                            //  sku 
                            picture: 'https://img.yzcdn.cn/1.jpg'
                        },
                    sku: {
                        // sku
                        // 
                        tree: [
                            {
                            k: '', // skuKeyName
                            v: [
                                {
                                    id: '30349', // skuValueId id
                                    name: '', // skuValueName
                                    imgUrl: 'https://img.yzcdn.cn/1.jpg' // 
                                },
                                {
                                    id: '1215',
                                    name: '',
                                    imgUrl: 'https://img.yzcdn.cn/2.jpg'
                                }
                            ],
                            k_s:'s2' // skuKeyStrsku  list key value  id
                            }
                        ],
                        //  sku M  sku S 
                        list: [
                            {
                            id: 2259, // skuId
                            price: 100, // 
                            s1: '1215', //  k_s  s1  id
                            s2: '30349', //  k_s  s2  id
                            s3: '0', // 30
                            stock_num: 110 //  sku 
                            }
                        ],
                            price: '1.00', // 
                            stock_num: 227, // 
                            collection_id: 2261, //  skuId  collection_id sku  id
                            none_sku: false, // 
                            hide_stock: false // 
                        }
                },
                active: 2
            }
        },
        components:{
            Pheader,
            GoodsAction,
            GoodsActionBigBtn,
            GoodsActionMiniBtn,
        },
        methods: {
            onChange(index) {
                this.index = index;
            },
            preview(index) {
                this.show = !this.show;
                this.index = index;
            },
            all() {
                this.showBase = true;
            }
        },
    }
</script>

<style lang="stylus" scoped>
img 
    width 100%
// 
.group_item
    padding-left 20px
    line-height 56px
.group_info 
    // padding-top 88px
.group_swipe
    width 100%
    height 500px
.group_notice
    height 40px
    line-height 40px
    text-align center
    color #fff
    background #fd5014
.mark_img
    float left
    width 100px
    height 100px
    margin-right 10px
    vertical-align top
.mark_text
    width 5px
.price
    color #ff2641
.price_text
    font-size 36px
.pName
    font-size 40px
.mark_item
    margin-top 10px
// 
.group_booking_all
    margin 20px 20px
    border-bottom 2px solid #ccc
    line-height 60px
.group_booking
    padding 0 20px
    margin 20px 0
    box-size border-box 
    -moz-box-size border-box 
    -webkit-box-size border-box 
.group_booking_left
    line-height  80px
.group_booking_img
    width 80px
    height 80px
    margin-right 20px
    border-radius 50%
    vertical-align  middle
.group_booking_right
    text-align right 
    line-height 40px
</style>


