import Axios from 'axios'
import router from '@/router'
//  || 
var root = process.env.API_ROOT;
const axios = Axios.create();
import { Dialog } from 'vant';
let cancel ,promiseArr = {} 
/* axios*/
axios.interceptors.request.use(
	config => {
		//url
		config.url = root + config.url;
		config.withCredentials = true // token ,
		config.timeout = 1000 // 
		// var token = window.sessionStorage.getItem("token");
		// if(token) {
		// 	config.headers = {
		// 		'Authorization': token,
		// 		'Content-Type': 'application/x-www-form-urlencoded'
		// 	}
		// }
		//
		if (promiseArr[config.url]) {
			promiseArr[config.url]('')
			promiseArr[config.url] = cancel
		} else {
			promiseArr[config.url] = cancel
		}
		return config
	},
	error => {
		console.log(error)
		return Promise.reject(error)
	})

// http response  //
axios.interceptors.response.use(
	response => {
		if(response.data['status'] === -1) {
			console.log('-');
			Dialog.alert({
				message: ''
			}).then(() => {
				window.sessionStorage.setItem("token",null);
				console.log('token,: ',window.sessionStorage.getItem("token"));
				/** */
				window.sessionStorage.setItem("this_router",window.location.href)
				console.log(' this_router: ',window.sessionStorage.getItem("this_router"));
				router.replace({  	
					path: '/index',
				})
			})
		}
		return response;
	},
	error => {
		if(error && error.response) {
				
		}else{
			error.message = ''
			console.log(error.message)
		}
		// console.log(Promise.reject(error.response.data))
		return Promise.reject(error.response.data)
	});

export default axios;


