<template>
    <div class="selectPoint">
        <div class="map">
            <iframe id="mapPage" width="100%" height="700px" frameborder=0
                src="https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=VFUBZ-JIR3D-Z2M4H-PPAGG-G5KVQ-S3F2S&referer=myapp">
            </iframe>
            <!-- <iframe id="mapPage" width="100%" height="700px" frameborder=0
                src="https://apis.map.qq.com/tools/locpicker?search=1&policy=1&type=0&backurl=http://3gimg.qq.com/lightmap/components/locationPicker2/back.html&key=VFUBZ-JIR3D-Z2M4H-PPAGG-G5KVQ-S3F2S&referer=myapp">
            </iframe> -->
        </div>
    </div>
</template>

<script>
export default {
    name:'selectPoint',
    data(){
        return {
           
        }
    },
     /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#ffffff',
                borderBottom :'1px solid #e6e6e6'
            },
            return_data: {
                icon: '/static/img/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);
        var that = this;
        window.addEventListener('message', function(event){
            // 
            var loc = event.data;
            if (loc && loc.module == 'locationPicker') {//postmodule'locationPicker'
                console.log('location', loc.poiaddress,loc.poiname);
                that.$router.push({name:'addAddress',params:{'poiaddress':loc.poiaddress,'poiname':loc.poiname}})
            }
        }, false);
    },
    mounted(){
        
    },

}
</script>

<style lang="stylus" scoped>
body
    position absolute
    width 100%
    height 100%
    text-align center
    .selectPoint
        min-height 100vh
        background-color #ffffff

    

            

</style>



