<template>
	<div class="zd_wrap">
		<div class="public_head head">
            <HeadTop></HeadTop>
        </div>
    </div>    
</template>

<script>
	/** */
	import HeadTop from '@/components/public/page_top_head'
	export default {
		data() {
			return{
                
			}
		},
		components: {
			HeadTop,
		},
		/*DOM*/
		created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: true,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

		},
		methods: {
             
		}
	}
</script>

<style lang="stylus" scoped>
	.zd_wrap
		width 100%
		height 100%
		background #fff
		.head
			border-bottom 1.1px solid #e6e6e6
</style>


