<template>
	<div class="sup_wrap">
		<div class="public_head head">
            <headtop></headtop>
        </div>
		<div class="content">
			<div class="row_wrap">
				<!--  -->
				<div class="name_wrap bor">
					<div class="name"></div>
					<div class="inp_wrap">
						<input type="text" placeholder=""/>
					</div>
				</div>
				<!--  -->
				<div class="name_wrap">
					<div class="name"></div>
					<div class="inp_wrap">
						<input type="text" placeholder=""/>
					</div>
				</div>
			</div>
			<!--  -->
			<div class="submit_btn"></div>
		</div>
        
		
	</div>
</template>

<script>
	/** */
	import headtop from '@/components/public/page_top_head'
	export default {
		name: 'applicationSupplier',
		components: {
			headtop,
		},
		data() {
			return{
                
			}
		},
		
		/*DOM*/
		created: function(){
			/**head=>style */
			var style_obj = {
				head_wrap_style: {
					background: '#fff',
				},
				return_data: {
					need_model: true,
					icon: '/static/img/public/left_icon_555.png',
					/*(),=>*/
					url_data: '',
				},
				title: {
					/**- */
					need_model: true,
					name: '',
					style: {color:'#151515'}
				},
				right_button: {
					need_model: true,
					/**=>=>  */
					text: '',
					/**-text */
			        change_text: '',
					/**=>(styleclass) */
					icon: {
						class: '',
						icon_url: '',
					},
					/**- */
					show: false,
					url_data: '',
				},
			}
			/**vuex */
			this.$store.commit('change_head',style_obj);

		},
		methods: {
		}
	}
</script>

<style lang="stylus" scoped>
.sup_wrap
	width 100%
	height 100%
	background #fff
	.head
		border-bottom 1.1px solid #e6e6e6
	.content
		.row_wrap
			padding 20px 29px
			margin 15px 24px
			background #fff
			box-shadow 0px 0px 5px 0px #cccccc
			-webkit-box-shadow 0px 0px 5px 0px #cccccc
			border-radius 6px
			.bor
				border-bottom 1.1px solid #e6e6e6
			.name_wrap
				padding 25px 0	
			.name_wrap .name,.inp_wrap
				display inline-block
				vertical-align top
			.name
				width 150px
				font-size 30px
				color #151515
				text-align right
				line-height 65px
			.inp_wrap
				margin-left 45px	
			.inp_wrap input
				width 400px
				border none
				outline none
				font-size 26px
				color #151515
				line-height 65px
			.inp_wrap input::-webkit-input-placeholder
				font-size 24px
				color #a0a0a0
		.submit_btn
			margin 650px auto
			width 702px
			height 88px
			background #ff4902
			border-radius 44px
			color #fff
			font-size 36px
			text-align center
			line-height 88px	

</style>


