<template>
    <div class="bindingTel">
        <!--  -->
        <headtop></headtop>
        <div class="content">
            <div class="form-group">            
                <div class="input-group">
                    <input type="number" v-model="tel" placeholder="11">
                </div>
                <div class="input-group">
                    <input type="text" placeholder="">
                    <button class="getVerifyCode" :disabled="disabled" @click="getVerifyCode()">{{verifyCode}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import headtop from '@/components/public/page_top_head'

import { Toast } from 'vant'
Vue.use(Toast)

export default {
    name:'bindingTel',
    components: {
        headtop
    },
    data(){
        return{
            tel:'',
            disabled:false,//true
            verifyCode:""
        }
    },
     /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#ffffff',
                borderBottom :'1px solid #e6e6e6'
            },
            return_data: {
                need_model: true,
                icon: '/static/img/public/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);
    },
    methods:{
        // 
        countDown(){
            var count = 60
            var timer = setInterval( () => {
                if(count == 0){
                    clearInterval(timer)
                    this.verifyCode = ''
                    this.disabled = false;
                }
                // 
                else if(count < 10){
                    count = '0' + count
                    this.verifyCode = count + "";
                    this.disabled = true;
                    count -- ;
                }else{
                    this.verifyCode = count + "";
                    this.disabled = true;
                    count -- ;
                }
            },1000);
        },
        // 
        validatePhone(){
            if(!this.tel){
                Toast('')
                return false;
            }else if(!/^1[345678]\d{9}$/.test(this.tel)){
                Toast('')
                return false;
            }else{
                return true;
            }
        },
        // 
        getVerifyCode(){
            if(this.validatePhone()){
                // 
                this.countDown();
            }
        },

    }
}
</script>

<style lang="stylus" scoped>
.bindingTel
    background-color #ffffff
    min-height 100vh
    .content
        padding 108px 24px
        box-sizing border-box
        .form-group
            box-shadow 1px 1px 8px #ccc
            padding 30px 
            box-sizing border-box
            .input-group
                height 56px
                display flex
                align-items center
                border-bottom 1px solid #e6e6e6
                padding-bottom 10px
                margin-bottom 25px
                input
                    font-size 24px
                    flex 1
                    height 60px
                    border none
                .getVerifyCode
                    font-size 28px
                    color #ff4902
                    padding 0 10px
                    height 60px
                    line-height 60px
                    border 1px solid #ff4902
                    font-size 28px
                    background none
                    border-radius 25px
            .input-group:last-child
                border none
                margin 0

</style>



